/*Para tablet peque√±a*/
@media (max-width: 740px){
    .wellcomezone{
    
         display: flex;
         margin: 0.1rem 0;
    }
    .titlezone{
        margin-left: 2rem;
        margin-top: 3%;
        font-size: 2em;
    }
}

/*Para celular*/

@media (max-width: 850px){
    .checkBtn{
        display: block;
    }
    ul{
        position: fixed;
        width: 100%;
        height: 100vh;
        background: #222;
        top: 80px;
        left: -100%;
        text-align: center;
        transition: all 0.5s;
    }
    nav ul li{
        display: block;
        margin: 5px 0;
        line-height: 30px;
    }
    nav ul li a{
        font-size: 20px;
    }
    li a:hover, li a.active{
        background: #107C69;
        color: wheat;
    }
   
    #check:checked ~ ul{
        left: 0;
    }

    .servicezone{
        display: flex;
        width: 100%;
        height: 85rem;
        flex-direction: column;
        align-items: center;
        
    }

    .wellcomezone{
        display: flex;
        justify-content: center;
        flex-direction: column;
        width: 90%;
        align-items: center;
        margin: 0 2rem;
    }

    .wellcomezone-left{
        display: flex;
        justify-content: center;
        width: 100%;
    }

    .testimonials{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 85rem;
        justify-content: space-around;
    }
    .group-testimonials{
        width: 90%;
        display: flex;
        flex-direction: column;
        border-bottom: 5rem;
    }

    .afterbanner{
        display: flex;
        flex-direction: column;
        height: 35rem;
    }
    .afterbanner div{
        width: 90%;
    }

    .phonezone{
        display: flex;
        align-items: center;
    }
    .phonezone img{
        width: 9rem;
        height: 14rem;
    }
    .Presentation{
        width: 90%;
    }
   
    .process-1{
        display: flex;
        width: 100%;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        justify-content: space-evenly;
        height: 30rem;
    }
    .process-1 img{
       display: flex;
       margin: 0px;
       padding: 0;
    }

    .process-1 div{
        width: 85%;
    }


    .process-2{
        display: flex;
        width: 90%;
        flex-direction: column;
        align-items: center;
        flex-direction: column-reverse;
        justify-content: center;
        justify-content: space-evenly;
        height: 30rem;
    }

    .process-3{
        display: flex;
        flex-direction: column;
        height: 25rem;
        width: 100%;
        justify-content: center;
        align-items: center;
        
    }
    .process-3 div{
       width: 90%;
 
    }
    .form-area{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 50rem;
        width: 100%;

    }



    footer{
        
        display: flex;
        flex-direction: column;
        margin: 1rem 2rem;
        padding: 1rem;
    }

    .social-media{
        width: 90%;
        
    }
    .social-media img{
        width: 24px;
    }

}

