.grid-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
  }
  
  .article {
    background-color: #f1f1f1;
    padding: 20px;
    text-align: center;
  }
  
  .article h2 {
    margin-top: 0;
  }
  
  .article a {
    display: block;
    margin-top: 10px;
  }
  