*{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
header{
    
    width: 100%;
    height: 5rem;
    
}
nav{
    background-color: #052D3F;
    width: 100%;
    height: 80px;
}

.fa-sharp.fa-solid.fa-star {
    font-size: 2em;  
     color: white;
  }
.fa-sharp.fa-solid.fa-compact-disc{
    font-size: 2em;
    color: rgb(204, 204, 204);

}
.fa-brands.fa-wpbeginner {
    font-size: 2em;
    color: #acf0f5;
}

.enlace{
    position: absolute;
    padding: 20px 50px;
}

.logo{
    height: 40px;
    background-color: white;
    border-radius: 50%;
}

nav ul{
    float: right;
    margin-right: 20px;
     
    
}

nav ul li{
    display: inline-block;
    line-height: 80px;
    margin: 0 5px;
     
}
nav ul li a{
    color: rgb(255, 255, 255);
    font-size: 18px;
    padding: 7px 13px;
    border: 3px;
}

li a.active, li a:hover{
    background: #FED69C;
    color: #052D3F;
    transition: .5s;
}

.checkBtn{
    font-size: 30px;
    color: white;
    float: right;
    line-height: 80px;
    margin-right: 40px;
    display: none;
}

#check{
    display: none;
}
 
.ContenedorG {
     
    height: 100%;
    width: 90vw;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    
  }

  .TableContents{
    display: flex;
    width: 100%;
    justify-content: center;
    background-color: #052d3f;
    height: 8rem;
    border-bottom: 1rem;
  }
  .TableContents a{
 
    color: rgb(233, 233, 233);
 
  }
  h1, main{
    display: flex;
    justify-content: center;
  }
  
  .texts{
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    align-items: center;
  }

  .homeContent{
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
  }

  ul{
    list-style: none;
  }

  .imgMain{
    display: flex;
    justify-content: center;
    width: 90vw;
  }
  .imgMain img{
    width: 350px;
  }

 